<template>
  <div id="selfOrder" v-loading="loading" element-loading-background="#f8f8f8">
    <el-row>
      <h2>订单信息</h2>
      <el-divider></el-divider>
      <div
        class="row"
        style="width: 100%; justify-content: space-around; margin-bottom: 20px"
      >
        <el-card style="margin: 0 10px; flex: 1; height: 80px">
          <div
            class="row"
            style="
              width: 100%;
              justify-content: space-around;
              margin-bottom: 20px;
            "
          >
            <i class="el-icon-s-claim icon"></i>
            <div class="col">
              <div>已完成订单数</div>
              <div style="color: #409eff; font-size: 20px">
                {{ havePay.length }}
              </div>
            </div>
          </div>
        </el-card>
        <el-card style="margin: 0 10px; flex: 1; height: 80px">
          <div
            class="row"
            style="
              width: 100%;
              justify-content: space-around;
              margin-bottom: 20px;
            "
          >
            <i class="el-icon-s-order icon"></i>
            <div class="col">
              <div>待支付订单数</div>
              <div style="color: #409eff; font-size: 20px">
                {{ waitPay.length }}
              </div>
            </div>
          </div>
        </el-card>
        <el-card style="margin: 0 10px; flex: 1; height: 80px">
          <div
            class="row"
            style="
              width: 100%;
              justify-content: space-around;
              margin-bottom: 20px;
            "
          >
            <i class="el-icon-position icon"></i>
            <div class="col">
              <div>旅程长度</div>
              <div style="color: #409eff; font-size: 20px">1000公里</div>
            </div>
          </div>
        </el-card>
      </div>
      <h3 style="color: #e6a23c">等待支付</h3>
      <div style="color: #c0c4cc; font-size: 12px; margin-bottom: 10px">
        目前处于排队等待支付的订单
      </div>
      <el-card
        style="margin: 10px 0; height: 120px; position: relative"
        shadow="hover"
        v-for="(order, index) in waitPay"
        :key="index"
        body-style="padding: 10px"
      >
        <div class="col">
          <div
            class="row"
            style="width: 100%; height: 100px; justify-content: start"
          >
            <div class="el-icon-document-remove wait"></div>
            <strong>订单编号：{{ order.orderId }}</strong>
            <el-statistic
              :value="
                20 * 1000 * 60 +
                Date.parse(order.orderTime.toString().slice(0, 19))
              "
              time-indices
              title="剩余支付时间"
              format="HH:mm:ss"
              style="width: 90px; position: absolute; right: 10px; top: 10px"
              :value-style="{ color: 'red' }"
            >
              <template slot="prefix">
                <i class="el-icon-timer" style="color: red"></i>
              </template>
            </el-statistic>
          </div>
        </div>
        <div
          style="display: flex; justify-content: flex-end; align-items: center"
        >
          <el-button
            type="primary"
            size="mini"
            style="position: absolute; bottom: 10px; right: 10px"
            v-if="
              20 * 1000 * 60 +
                Date.parse(order.orderTime.toString().slice(0, 19)) >
              0
            "
            @click="pay(order)"
            >继续支付</el-button
          >
        </div>
      </el-card>
      <h3 style="color: #67c23a">已支付</h3>
      <div style="color: #c0c4cc; font-size: 12px; margin-bottom: 10px">
        历史记录已支付的订单
      </div>
      <el-card
        style="margin: 10px 0; height: 120px; position: relative"
        shadow="hover"
        v-for="(order, index) in havePay"
        :key="index"
        body-style="padding: 10px"
      >
        <div class="col">
          <div
            class="row"
            style="width: 100%; height: 100px; justify-content: start"
          >
            <div class="el-icon-document-checked have"></div>
            <strong>订单编号：{{ order.orderId }}</strong>
          </div>
        </div>
        <div
          style="display: flex; justify-content: flex-end; align-items: center"
        >
          <el-button
            type="primary"
            size="mini"
            style="position: absolute; bottom: 10px; right: 100px"
            @click="
              reDialog = true;
              getDetail(order);
            "
            >改签/退票</el-button
          >
          <el-button
            type="primary"
            size="mini"
            style="position: absolute; bottom: 10px; right: 10px"
            @click="getDetail(order)"
            >查看详情</el-button
          >
        </div>
      </el-card>
    </el-row>

    <el-dialog title="订单详情" :visible.sync="dialog">
      <div v-if="details.vehicleType == '飞机'">
        <el-descriptions direction="vertical" :column="3" border>
          <el-descriptions-item label="订单编号" :span="3">{{
            details.info[0].orderId
          }}</el-descriptions-item>
          <el-descriptions-item label="航班">{{
            details.info[0].flightId
          }}</el-descriptions-item>
          <el-descriptions-item label="航线"
            >{{ details.info[0].flightStartPort }} -{{
              details.info[0].flightEndPort
            }}</el-descriptions-item
          >
          <el-descriptions-item label="时间"
            >{{ details.info[0].flightStartTime.toString().slice(0, 19) }} 至{{
              details.info[0].flightEndTime.toString().slice(0, 19)
            }}</el-descriptions-item
          >
          <el-descriptions-item label="乘客"
            ><span v-for="(item, index) in details.info" :key="index">
              {{ item.passengerName }}
            </span></el-descriptions-item
          >
          <el-descriptions-item label="座位">{{
            details.info[0].seatType
          }}</el-descriptions-item>
          <el-descriptions-item label="价格"
            >{{
              details.info[0].flightPrice * details.info.length
            }}￥</el-descriptions-item
          >
        </el-descriptions>
      </div>
      <div v-else-if="details.vehicleType == '火车'">
        <el-descriptions direction="vertical" :column="3" border>
          <el-descriptions-item label="订单编号" :span="3">{{
            details.info[0].orderId
          }}</el-descriptions-item>
          <el-descriptions-item label="车次">{{
            details.info[0].trainNumberId
          }}</el-descriptions-item>
          <el-descriptions-item label="线路"
            >{{ details.info[0].trainStartStation }} -{{
              details.info[0].trainEndStation
            }}</el-descriptions-item
          >
          <el-descriptions-item label="时间"
            >{{
              details.info[0].trainTicketStartTime.toString().slice(0, 19)
            }}至{{ details.info[0].trainTicketEndTime.toString().slice(0, 19) }}
          </el-descriptions-item>
          <el-descriptions-item label="乘客"
            ><span v-for="(item, index) in details.info" :key="index">
              {{ item.passengerName }}
            </span>
          </el-descriptions-item>
          <el-descriptions-item label="座位"
            >{{ details.info[0].seatType }}
          </el-descriptions-item>
          <el-descriptions-item label="价格"
            >{{
              details.info[0].trainPrice * details.info.length
            }}￥</el-descriptions-item
          >
        </el-descriptions>
      </div>
    </el-dialog>
    <el-dialog title="改签/退款" :visible.sync="reDialog">
      <el-popover placement="top-start" width="900" trigger="hover">
        <div slot="reference" style="color: #78bba0; width: 100px">
          退改签说明
        </div>
        <strong>成人票</strong>
        <el-descriptions
          title="退票费"
          direction="vertical"
          :column="4"
          border
          style="width: 100%"
        >
          <el-descriptions-item label=">7天" contentStyle="color: #ffd866"
            >10%</el-descriptions-item
          >
          <el-descriptions-item label="3-7天" contentStyle="color: #ffd866"
            >30%</el-descriptions-item
          >
          <el-descriptions-item label="1-3天" contentStyle="color: #ffd866"
            >50%</el-descriptions-item
          >
          <el-descriptions-item label="<1天" contentStyle="color: #ffd866"
            >70%</el-descriptions-item
          >
        </el-descriptions>
        <el-descriptions
          title="同舱改期费"
          direction="vertical"
          :column="4"
          border
          style="width: 100%"
        >
          <el-descriptions-item label=">7天" contentStyle="color: #ffd866"
            >5%</el-descriptions-item
          >
          <el-descriptions-item label="3-7天" contentStyle="color: #ffd866"
            >15%</el-descriptions-item
          >
          <el-descriptions-item label="1-3天" contentStyle="color: #ffd866"
            >35%</el-descriptions-item
          >
          <el-descriptions-item label="<1天" contentStyle="color: #ffd866"
            >55%</el-descriptions-item
          >
        </el-descriptions>
        <div class="row" style="margin: 10px 0">
          <strong>签转条件</strong>
          <div>不得签转</div>
        </div>
        <div class="row">
          <strong>行李额</strong>
          <div class="col">
            <div>
              免费手提行李额：每件10KG,每人1件。单件行李质量不超过10KG,单件体积不超过20*40*55CM
            </div>
            <div>
              免费托运行李额：每件23KG,每人1件。单件托运行李质量不超过50KG,单件体积不超过40*60*100CM
            </div>
          </div>
        </div>
        <div class="row" style="margin: 10px 0">
          <strong>报销凭证</strong>
          <div>
            可提供”行程单”作为报销凭证，凭证金颜以实际开具金额为准，
            支付成功后可查看订单并开具凭证
          </div>
        </div>
        <div class="row" style="margin: 10px 0">
          <strong>航司备注</strong>
          <div style="width: 700px">
            手提行李标准：单件重量不能超过10千克且体积不能超过20×40×55厘米可带入客舱的行李箱。如果单件手提行李体积超过20×40×55厘
            米，或重量超过10千克的，需办理行李托运。托运行李标准：单件重量不能超过50千克且单件体积不能超过40×60×100厘米。单件体积
            超过40×60×100厘米的行李需按照特殊行李执行或须事先征得同意方能托运。
          </div>
        </div>
        <div class="row" style="margin: 10px 0">
          <strong>备注</strong>
          <div>
            航班延误或取消，不提供经济补偿。航班取消、延误15分钟以上可按非自愿退票办理
          </div>
        </div>
      </el-popover>
      <el-tabs v-model="activeName" :stretch="true">
        <el-tab-pane label="改签" name="first">
          <div v-loading="true" style="width: 100%; height: 200px"></div>
        </el-tab-pane>
        <el-tab-pane label="退款" name="second">
          <div
            style="
              width: 100%;
              height: 200px;
              position: relative;
              overflow: hidden;
            "
          >
            <el-select
              v-model="value"
              placeholder="退款理由"
              style="
                position: absolute;
                top: 10px;
                left: calc(50% - 100px);
                width: 200px;
              "
            >
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
            <el-button
              type="primary"
              @click="refund"
              style="
                position: absolute;
                bottom: 10px;
                left: calc(50% - 100px);
                width: 200px;
              "
              >申请退款</el-button
            >
          </div>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
    <div v-html="alipay" ref="alipay"></div>
  </div>
</template>

<script>
import axios from "axios";
import webconfig from "@/data/config";
import { mapActions } from "vuex";
export default {
  data() {
    return {
      loading: true,
      dialog: false,
      orderList: [],
      havePay: "",
      waitPay: "",
      details: "",
      alipay: "",
      reDialog: false,
      activeName: "first",
      options: [
        {
          value: "行程改变",
          label: "行程改变",
        },
        {
          value: "价格变动",
          label: "价格变动",
        },
        {
          value: "航班延误",
          label: "航班延误",
        },
        {
          value: "其他原因",
          label: "其他原因",
        },
      ],
      value: "",
    };
  },
  methods: {
    ...mapActions("login", { getInfo: "getInfo" }),
    getOrders() {
      axios({
        method: "get",
        url: `${webconfig.url}/whole-order/getOrder`,
        headers: { token: localStorage.getItem("token") },
        params: { usrId: this.$store.state.user.userForm.usrId },
      })
        .then((res) => {
          console.log(res.data.data);
          this.orderList = res.data.data;
          this.havePay = this.orderList.filter((item) => {
            return item.orderStatus == "已支付";
          });
          this.waitPay = this.orderList.filter((item) => {
            return item.orderStatus == "未支付";
          });
        })
        .catch((err) => {
          console.log(err);
        });
    },
    details() {
      this.dialog = true;
    },
    getDetail(order) {
      let url =
        webconfig.url +
        (order.vehicleType == "飞机"
          ? "/aircraft/query-aircraft-ticket"
          : "/train/query-train-ticket");
      axios({
        method: "get",
        url: url,
        headers: { token: localStorage.getItem("token") },
        params: { orderId: order.orderId },
      })
        .then((res) => {
          console.log(res.data.data);
          this.details = res.data.data;
          this.details = {
            info: this.details,
            vehicleType: order.vehicleType,
          };

          console.log(this.details);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    pay(order) {
      axios({
        method: "get",
        url: `${webconfig.url}/whole-order/alipayContinue`,
        headers: { token: localStorage.getItem("token") },
        params: { orderId: order.orderId },
      })
        .then((res) => {
          this.alipay = res.data.data;
          this.$nextTick(() => {
            this.$refs.alipay.children[0].submit();
          });
        })
        .catch((err) => {
          console.log(err);
        });
    },
    refund() {
      console.log(this.details);
      axios({
        method: "post",
        url: `${webconfig.url}/usr/alipay-refund`,
        headers: { token: localStorage.getItem("token") },
        params: {
          orderId: this.details.info[0].orderId,
          money:
            this.details.vehicleType == "火车"
              ? this.details.info[0].trainPrice * this.details.info.length
              : this.details.info[0].flightPrice * this.details.info.length,
          reason: this.value,
        },
      })
        .then((res) => {
          console.log(res.data.data);
          if (res.data.code == 200) {
            this.$message({
              message: "退款成功",
              type: "success",
            });
            this.reDialog = false;
            this.getOrders();
          } else {
            this.$message({
              message: res.data.msg,
              type: "error",
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  activated() {
    this.getInfo(this);
    this.getOrders();
    setTimeout(() => {
      this.loading = false;
    }, 1000);
  },
  deactivated() {
    console.log("deactivated");
    this.loading = true;
  },
};
</script>

<style scoped>
strong {
  width: 100px;
  margin-right: 5px;
}
.icon {
  font-size: 45px;
  color: #409eff;
  background-color: white;
  transition: all 0.3s;
}
#selfOrder {
  padding: 30px 25px;
}
.row {
  display: flex;
  justify-content: start;
  align-items: center;
}
.col {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: start;
}
.wait {
  font-size: 50px;
  margin-right: 20px;
  color: #e6a23c;
  background-color: white;
  transition: all 0.3s;
}
.wait:hover {
  font-size: 50px;
  color: white;
  background-color: #e6a23c;
  transition: all 0.3s;
}
.have {
  font-size: 50px;
  margin-right: 20px;
  color: #3ff375;
  background-color: white;
  transition: all 0.3s;
}
.have:hover {
  font-size: 50px;
  color: white;
  background-color: #3ff375;
  transition: all 0.3s;
}
</style>
